import { _decorator, Component, Node, Label, Sprite, tween, Quat, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('loading')
export class loading extends Component {

    @property(Sprite)
    loading_spr: Sprite;

    public ex = 0
    public ey = 0;
    public ez = 0;
    public startR = false;

    start() {
        var sprnode = this.loading_spr.node
    }

    onEnable() {

    }

    update(deltaTime: number) {

        if(this.startR)
        {
            var sprnode = this.loading_spr.node;
            this.ex = this.ex + 1;
            this.ey = this.ey + 1;
            this.ez = this.ez + 1;
            sprnode.setRotationFromEuler(0, 0, this.ez);
        }
        


        
        // let quat1 = new Quat();
        // let r = sprnode.getRotation(quat1);
        // r.y += 0.002;
        // if (r.y > 2) {
        //     r.y = 0;
        // }
        // sprnode.setRotation(r);
    }
}


